/* Above Tablet */
@media only screen and (min-width: 1024px) {
}

/* Both Tablet and Mobile */
@media only screen and (max-width: 1023px) {
}

/* Tablet only */
@media only screen and (min-width: 768px) and (max-width: 1023px) {
}

/* Mobile only */
@media only screen and (max-width: 767px) {
    .wel_text{
        font-size: 2rem;
    }
    .event_tit1{
        font-size: 1.5rem;
    }
    .event_tit2{
        font-size: 1.5rem;
    }
}
